<template>
  <div id="app">
    <img class="logo" alt="Vue logo" src="./assets/logo.png">
    <h1 class="app__heading">To-Do List</h1>
    <inputWrap 
      v-model="text"
      @submit="submit"
    />
    <taskList 
      :items="items"
      :sortBlock="sortShow"
    />

    <div class="sort" 
         v-if="sortShow"
         @click="colorSort"
    >
      <h4 class="sort__heading">Sorting list by color</h4>
      <button class="sort__button">Sort</button>
    </div>

  </div>
</template>

<script>
import inputWrap from './components/input.vue'
import taskList from './components/taskList.vue'

export default {
  name: 'app',
  data() {
    return {
      text: "",
      firstId: 0,
      items: [],
      sortShow: false
    }
  },
  methods: {
    submit() {
      let date = new Date();
      let options = {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric'
      };
      this.items.push({
        id: this.firstId,
        text: this.text,
        time: date.toLocaleString("ru", options), 
        modal: false,
        color: 'red',
        done: false
      });

      this.text = "";
      this.firstId++;

      this.showSortBlock();
    },
    showSortBlock() {
      this.firstId>=1 ? this.sortShow=true : this.sortShow=false;
    },
    colorSort() {
      let arr = this.items;
      let itemsLenght = arr.length;

      function compare(a,b) {
        if (a.color < b.color)
          return -1;
        if (a.color > b.color)
          return 1;
        return 0;
      }

      arr.sort(compare);
    }
  },
  components: {
    inputWrap,
    taskList
  }
}
</script>

