<script>
import Modal from './Modal.vue'

export default {
  name: 'taskList',
  props: ['items', 'sortBlock'],
  methods: {
  	modalShow(item) {
  		item.modal = !item.modal;
  	},
  	doneHandler(item) {
  		item.done = !item.done;
  	},
  	changeColor(newColor, item) {
  		item.color = newColor;
  		item.modal = false;
  	}
  },
  components: {
    Modal
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.task-list {
		width: 20%;
		/*max-width: 40%;*/
		margin: 0 auto;
		margin-bottom: 40px;
		padding: 0;
	}

	.task-list__item {
		position: relative;
		list-style: none;
		margin-bottom: 7px;
		padding: 5px 5px 5px 25px;
		box-sizing: border-box;
		border: 1px solid #000;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		font-size: 0.9rem;
		transition: all 0.3s;
	}

	.task-list__icon_done:hover {
		fill: #00ff11;
	}

	.task-list__text {
		text-align: left;
		width: 100%;
	}
	.task-list__time {
		font-size: 0.55rem;
		width: 63px;
		text-align: right;
	}

	.task-list__icon {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 20px;
		height: 20px;
		fill: #000;
		transition: all 0.3s;
		opacity: 0.6;
		cursor: pointer;
	}
	.task-list__icon_done {
		left: -30px;
	}
	.task-list__icon_settings {
		right: -30px;
		cursor: pointer;
	}
	.task-list__icon_settings:hover {
		opacity: 1;
	}    

	.color-box {
		position: absolute;
		border-right: 1px solid #000;
		left: 0;
		top: 0;
		height: 100%;
		width: 15px;
		transition: all 0.3s;
	}
	.color-box_green {
		background-color: green;
	}
	.color-box_red {
		background-color: red;
	}
	.color-box_blue {
		background-color: blue;
	}
	.color-box_yellow {
		background-color: yellow;
	}

	.done {
		position: relative;
		border-color: #999;
		background-color: #9999;
		opacity: 0.4;
	}
	.done:before {
		position: absolute;
		top: 50%;
		left: 55%;
		transform: translateX(-50%);
		width: 110%;
		height: 1px;
		background-color: #000;
		content: "";
		z-index: 2;
	}
	.done .color-box {
		background-color: #9999;
	}
	.done .task-list__icon_setting {
		display: none;
	}


	.list-enter-active, .list-leave-active {
	  transition: all 0.5s;
	}
	.list-enter, .list-leave-to {
	  opacity: 0;
	  transform: translateY(10px);
	}
	.list-move {
  		transition: all 0.5s;
	}
</style>
