<template>
    <div class="modal">
      <p class="modal__heading">Change color</p>
      <ul class="meaning">
        <li @click="$emit('changeColor', 'red', item)" class="meaning__item"></li>
        <li @click="$emit('changeColor', 'green', item)" class="meaning__item"></li>
        <li @click="$emit('changeColor', 'blue', item)" class="meaning__item"></li>
        <li @click="$emit('changeColor', 'yellow', item)" class="meaning__item"></li>
      </ul>
      <p @click="deleteItem" class="modal__delete">delete</p>
    </div>
</template>

<script>
export default {
  name: 'Modal',
  props: ['item', 'sortBlock'],
  methods: {
    deleteItem(e) {
      var target = e.target;
      var listLength = document.getElementsByClassName("task-list__item").length;
      // var sort = document.getElementsByClassName("sort");
      target.parentElement.parentElement.remove();
      // if(listLength<2) {
      //   sort[0].style.display="none";
      // }
    }
  }
}
</script>

<style scoped>
  .modal {
    width: 150px;
    position: absolute;
    top: -100%;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 10px;
    box-sizing: border-box;
    border: 1px solid #000;
    background-color: #999;
    z-index: 2;
  }

  .modal__heading {
    color: #fff;
    margin: 0;
    margin-bottom: 10px;
  }
  .meaning {
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 0;
    margin-bottom: 10px;
  }
  .meaning__item {
    width: 15px;
    height: 15px;
    border: 1px solid #000;
    list-style: none;
    cursor: pointer;
  }
  .meaning__item:nth-child(1) {
      background-color: red;
  }
  .meaning__item:nth-child(2) {
      background-color: green;
  }
  .meaning__item:nth-child(3) {
      background-color: blue;
  }
  .meaning__item:nth-child(4) {
      background-color: yellow;
  }

  .modal__delete {
    color: #000;
    font-style: bold;
    margin: 0;
